<template>
  <div>
    <div class="immersive-mode-container" :class="{off: !immersiveMode}">
      <div class="setting-main-switch">
        <div class="setting-infos">
          <h2 class="setting-name">
            {{$i18n('settingsImmersiveModeTitle')}}
            <md-button class="md-icon-button md-dense" @click="immersiveModeTipsDisplay = true;">
              <md-icon>help_outline</md-icon>
            </md-button>
          </h2>
          <p class="setting-desc">{{$i18n('settingsImmersiveModeDesc')}}</p>
        </div>
        <md-switch v-model="immersiveMode"></md-switch>
      </div>
      <h3 class="block-name">{{$i18n('sectionWhiteListTitle')}}</h3>
      <!-- <h3 class="block-desc">{{$i18n('sectionWhiteListDesc')}}</h3> -->
      <md-content class="domains md-scrollbar">
        <div class="white-list-item" v-for="(whiteItem,index) in whiteList" :key="whiteItem">
          <img class="domain-icon" :src="getFavicon(whiteItem)" @error="fixIcon" />
          <div class="domain">{{whiteItem}}</div>
          <md-menu>
            <md-button class="md-icon-button" md-menu-trigger>
              <md-icon>more_vert</md-icon>
            </md-button>

            <md-menu-content>
              <md-menu-item @click="editWhiteItem(index)">
                <span>{{$i18n('editWhiteListItem')}}</span>
              </md-menu-item>
              <md-menu-item @click="removeWhiteItem(index)">
                <span>{{$i18n('delWhiteListItem')}}</span>
              </md-menu-item>
            </md-menu-content>
          </md-menu>
        </div>
      </md-content>
      <a class="add-site-btn" @click="showAddWhiteListDialog">{{$i18n('sectionWhiteListAddBtn')}}</a>
    </div>

    <md-dialog-alert
      :md-active.sync="immersiveModeTipsDisplay"
      :md-title="$i18n('tipsImmersiveModeTitle')"
      :md-content="$i18n('tipsImmersiveModeContent')"
      :md-confirm-text="$i18n('tipsImmersiveModeButton')"
    />

    <md-dialog
      :md-active.sync="dialog.addWhiteItem"
      :md-fullscreen="false"
      @md-opened="setAddDomainInputFocus"
    >
      <md-dialog-title>{{$i18n('addWhiteListItemDialogTitle')}}</md-dialog-title>
      <md-dialog-content>
        <md-field :class="{'md-invalid': invalidUrl}">
          <md-input
            ref="addDomainInput"
            v-model="whiteDomain"
            :placeholder="$i18n('whiteListItemInputPlaceholder')"
            @keydown.enter.native="confirmWhiteItem"
          />
          <span class="md-error">{{$i18n('invalidDomain')}}</span>
        </md-field>
      </md-dialog-content>

      <md-dialog-actions>
        <md-button
          class="md-primary"
          @click="cancelAddWhiteItem"
        >{{$i18n('whiteListItemDialogCancelBtn')}}</md-button>
        <md-button
          class="md-primary"
          @click="confirmWhiteItem"
          :disabled="!whiteDomain"
        >{{$i18n('addWhiteListItemDialogConfirmBtn')}}</md-button>
      </md-dialog-actions>
    </md-dialog>

    <md-dialog
      :md-active.sync="dialog.editWhiteItem"
      :md-fullscreen="false"
      @md-opened="setEditDomainInputFocus"
    >
      <md-dialog-title>{{$i18n('editWhiteListItemDialogTitle')}}</md-dialog-title>
      <md-dialog-content>
        <md-field :class="{'md-invalid': invalidUrl}">
          <md-input
            ref="editDomainInput"
            v-model="whiteDomain"
            :placeholder="$i18n('whiteListItemInputPlaceholder')"
            @keydown.enter.native="confirmWhiteItem"
          />
          <span class="md-error">{{$i18n('invalidDomain')}}</span>
        </md-field>
      </md-dialog-content>

      <md-dialog-actions>
        <md-button
          class="md-primary"
          @click="cancelEditWhiteItem"
        >{{$i18n('whiteListItemDialogCancelBtn')}}</md-button>
        <md-button
          class="md-primary"
          @click="confirmWhiteItem"
          :disabled="!whiteDomain"
        >{{$i18n('editWhiteListItemDialogConfirmBtn')}}</md-button>
      </md-dialog-actions>
    </md-dialog>

    <md-dialog :md-active.sync="dialog.removeWhiteItem" :md-fullscreen="false">
      <md-dialog-title>{{$i18n('delWhiteListItemDialogTitle')}}</md-dialog-title>
      <md-dialog-content v-if="currentWhiteListItem !== -1">
        <img
          class="remove-dialog-icon"
          :src="getFavicon(whiteList[currentWhiteListItem])"
          @error="fixIcon"
        />
        <span class="remove-dialog-content">{{whiteList[currentWhiteListItem]}}</span>
      </md-dialog-content>
      <md-dialog-actions>
        <md-button @click="cancelRemove">{{$i18n('whiteListItemDialogCancelBtn')}}</md-button>
        <md-button
          class="md-primary"
          @click="confirmRemove"
        >{{$i18n('delWhiteListItemDialogConfirmBtn')}}</md-button>
      </md-dialog-actions>
    </md-dialog>
  </div>
</template>

<script>
/* eslint-disable no-debugger */
/* eslint-disable no-console */
/* global chrome */
import SiteIcon from "@/assets/site-icon.png";

export default {
  name: "SettingsImmersiveMode",
  components: {},
  data: () => {
    const bgPage = chrome.extension.getBackgroundPage();
    return {
      whiteDomain: "",
      blackDomain: "",
      immersiveModeTipsDisplay: false,
      invalidUrl: false,
      currentWhiteListItem: -1,
      dialog: {
        addWhiteItem: false,
        editWhiteItem: false,
        removeWhiteItem: false
      },
      bgPage,
      immersiveMode: bgPage.settings.immersiveMode,
      whiteList: bgPage.settings.whiteList,
      blackList: bgPage.settings.blackList
    };
  },
  watch: {
    whiteDomain() {
      this.invalidUrl = false;
    },
    immersiveMode(val) {
      if (this.bgPage.settings.immersiveMode !== val) {
        this.bgPage.settings.immersiveMode = val;
      }
    },
    whiteList(val) {
      if (this.bgPage.settings.whiteList !== val) {
        this.bgPage.settings.whiteList = val;
      }
    },
    blackList(val) {
      if (this.bgPage.settings.blackList !== val) {
        this.bgPage.settings.blackList = val;
      }
    }
  },
  async created() {},
  methods: {
    async settingItem(key, defaultValue) {
      return new Promise(resolve => {
        chrome.storage.sync.get(key, res => {
          const val = res[key];
          if (val === undefined) {
            resolve(defaultValue);
          } else {
            resolve(val);
          }
        });
      });
    },
    getFavicon(url) {
      let fullUrl;
      if (url.startsWith("http://")) {
        fullUrl = url;
      } else if (url.startsWith("https://")) {
        fullUrl = url;
      } else {
        fullUrl = `http://${url}`;
      }
      return `${fullUrl}/favicon.ico`;
    },
    fixIcon(e) {
      const img = e.target;
      img.setAttribute("src", null);
      img.setAttribute("src", SiteIcon);
    },
    showAddWhiteListDialog() {
      this.currentWhiteListItem = -1;
      this.dialog.addWhiteItem = true;
    },
    setAddDomainInputFocus() {
      window.setTimeout(() => {
        this.$refs.addDomainInput.$el.focus();
      }, 50);
    },
    setEditDomainInputFocus() {
      window.setTimeout(() => {
        this.$refs.editDomainInput.$el.focus();
      }, 50);
    },
    confirmWhiteItem() {
      const re = /^(?:(?:https?|ftp):\/\/)?(localhost|(?=(\b|\D))(((\d{1,2})|(1\d{1,2})|(2[0-4]\d)|(25[0-5]))\.){3}((\d{1,2})|(1\d{1,2})|(2[0-4]\d)|(25[0-5]))(?=(\b|\D))|\s*((([0-9A-Fa-f]{1,4}:){7}([0-9A-Fa-f]{1,4}|:))|(([0-9A-Fa-f]{1,4}:){6}(:[0-9A-Fa-f]{1,4}|((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3})|:))|(([0-9A-Fa-f]{1,4}:){5}(((:[0-9A-Fa-f]{1,4}){1,2})|:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3})|:))|(([0-9A-Fa-f]{1,4}:){4}(((:[0-9A-Fa-f]{1,4}){1,3})|((:[0-9A-Fa-f]{1,4})?:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3}))|:))|(([0-9A-Fa-f]{1,4}:){3}(((:[0-9A-Fa-f]{1,4}){1,4})|((:[0-9A-Fa-f]{1,4}){0,2}:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3}))|:))|(([0-9A-Fa-f]{1,4}:){2}(((:[0-9A-Fa-f]{1,4}){1,5})|((:[0-9A-Fa-f]{1,4}){0,3}:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3}))|:))|(([0-9A-Fa-f]{1,4}:){1}(((:[0-9A-Fa-f]{1,4}){1,6})|((:[0-9A-Fa-f]{1,4}){0,4}:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3}))|:))|(:(((:[0-9A-Fa-f]{1,4}){1,7})|((:[0-9A-Fa-f]{1,4}){0,5}:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3}))|:)))(%.+)?\s*$|(?:(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,})))(?::\d{2,5})?(?:\/\S*)?$/i;
      if (!re.test(this.whiteDomain)) {
        this.invalidUrl = true;
      } else {
        let whiteDomain = this.whiteDomain;
        whiteDomain = whiteDomain.replace(/^\w*:\/\//g, "");
        whiteDomain = whiteDomain.split("/")[0];
        if (!this.whiteList.includes(whiteDomain)) {
          if (this.currentWhiteListItem === -1) {
            this.whiteList.unshift(whiteDomain);
          } else {
            const whiteList = [...this.whiteList];
            whiteList[this.currentWhiteListItem] = whiteDomain;
            this.whiteList = whiteList;
          }
        }
        this.dialog.addWhiteItem = false;
        this.dialog.editWhiteItem = false;
        this.invalidUrl = false;
        this.whiteDomain = "";
      }
    },
    cancelAddWhiteItem() {
      this.dialog.addWhiteItem = false;
      this.whiteDomain = "";
      this.invalidUrl = false;
    },
    editWhiteItem(index) {
      this.currentWhiteListItem = index;
      this.whiteDomain = this.whiteList[index];
      this.dialog.editWhiteItem = true;
      this.invalidUrl = false;
    },
    cancelEditWhiteItem() {
      this.dialog.editWhiteItem = false;
      this.whiteDomain = "";
      this.invalidUrl = false;
    },
    removeWhiteItem(index) {
      this.currentWhiteListItem = index;
      this.dialog.removeWhiteItem = true;
    },
    confirmRemove() {
      this.whiteList.splice(this.currentWhiteListItem, 1);
      this.dialog.removeWhiteItem = false;
      this.currentWhiteListItem = -1;
    },
    cancelRemove() {
      this.dialog.removeWhiteItem = false;
      this.currentWhiteListItem = -1;
    }
  },
  mounted() {
    chrome.storage.onChanged.addListener(changes => {
      if (changes.settings) {
        const newSettings = changes.settings.newValue;
        this.immersiveMode = newSettings.immersiveMode;
        this.whiteList = newSettings.whiteList;
        this.blackList = newSettings.blackList;
      }
    });
  }
};
</script>

<style lang="less" scoped>
.immersive-mode-container {
  margin-top: 32px;
  padding-bottom: 24px;
  border-radius: 8px;
  border: 1px solid @G5;
  transition: max-height 0.3s ease-out;
  max-height: 624px;
  overflow: hidden;
}

.immersive-mode-container.off {
  transition: max-height 0.3s ease-in;
  max-height: 136px;
}

.domains {
  overflow: auto;
  margin: 16px 24px 0 48px;
  padding-bottom: 16px;
  max-height: 320px;
  border-bottom: 1px solid @G4;
}

.add-site-btn {
  .font(14px, 16px);
  font-weight: 500;
  margin: 16px 0 16px 48px;
  display: inline-block;
  color: @Pink;
  text-decoration: none !important;
  cursor: pointer;
}

.white-list-item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 60px;
}

.domain-icon {
  width: 20px;
  height: 20px;
}

.domain {
  .font(16px, 19px);
  padding-left: 16px;
  flex-grow: 1;
  color: @G1;
}

.remove-dialog-icon {
  width: 18px;
  height: 18px;
  margin-right: 16px;
}
</style>