<template>
  <a :href="`mailto:talk@moreless.io?subject=${$i18n('subject')}&&body=${body}`">
    <slot></slot>
  </a>
</template>

<script>
/* global chrome */
function formatTime(date, fmt) {
  fmt = fmt || "yyyy-MM-dd hh:mm:ss";
  date = new Date(date);
  var o = {
    "M+": date.getMonth() + 1, //
    "d+": date.getDate(), //
    "h+": date.getHours(), //
    "m+": date.getMinutes(), //
    "s+": date.getSeconds(), //
    "q+": Math.floor((date.getMonth() + 3) / 3), //
    S: date.getMilliseconds() //
  };
  if (/(y+)/.test(fmt))
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + "").substr(4 - RegExp.$1.length)
    );
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt))
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length)
      );
  return fmt;
}

function getOSInfo(appVer) {
  let chrome_browser_version, chrome_os_version, operating_system;
  var strComp = appVer;
  var strComp1 = strComp.split("(").join(";");
  var strComp2 = strComp1.split(")").join(";");
  var strComp3 = strComp2.split("/").join(";");

  var arrStr = strComp3.split(";");
  var str1 = arrStr[1];
  var str2 = "NA";
  var str3 = "";
  var str4 = arrStr[1].substring(0, 7);
  var str5 = arrStr[2];

  if (str1 == "X11") {
    str2 = arrStr[2].substring(12, 25);
  }
  if (str1 == "X11") {
    str3 = navigator.platform + " (Chrome Book)";
  } else {
    str3 = arrStr[2];
    if (str3 == "WOW64") {
      str3 = "Windows 32-bit on Windows 64-bit";
    }
  }

  if (str4 === "Windows" && str5 === " Win64") {
    operating_system = str3;
    chrome_browser_version = arrStr[8].split(" ")[0];
    chrome_os_version = str2;
  } else {
    operating_system = str3;
    chrome_browser_version = arrStr[7].split(" ")[0];
    chrome_os_version = str2;
  }
  return {
    operating_system,
    chrome_os_version,
    chrome_browser_version
  };
}

export default {
  name: "Mailto",
  components: {},
  props: {
    subject: String,
    userInfos: Object
  },
  data() {
    const manifestData = chrome.runtime.getManifest();
    const OSInfos = getOSInfo(navigator.appVersion);
    return {
      appVersion: manifestData.version,
      OSInfos
    };
  },
  computed: {
    body() {
      return (
        `` +
        `%0d%0a` +
        `%0d%0a` +
        `%0d%0a` +
        `%0d%0a` +
        `%0d%0a--------------------` +
        `%0d%0a` +
        `%0d%0a App Version: ${this.appVersion}` +
        `%0d%0a Platform: Chrome Extension` +
        `%0d%0a Chrome Browser Version: ${this.OSInfos.chrome_browser_version}` +
        `%0d%0a Operating System: ${this.OSInfos.operating_system}` +
        (this.userInfos ? `%0d%0a User: ${this.userInfos.id}` : "") +
        `%0d%0a Date: ${formatTime(new Date())}`
      );
    }
  }
};
</script>

<style lang='less' scoped></style>
