<template>
  <div>
    <template v-if="records">
      <div class="records-container">
        <div class="records-item">
          <div class="record">
            <div class="record-data">{{records.today_times}}</div>
            <span class="record-data-name">{{$i18n('todayFocusTimes')}}</span>
          </div>
          <div class="record">
            <div class="record-data">
              {{parseInt(records.today_duration / 60)}}
              <span class="record-unit">{{$i18n('min')}}</span>
            </div>
            <span class="record-data-name">{{$i18n('todayFocusDurations')}}</span>
          </div>
        </div>
        <div class="records-item">
          <div class="record">
            <div class="record-data">{{records.this_week_times}}</div>
            <span class="record-data-name">{{$i18n('weeklyFocusTimes')}}</span>
          </div>
          <div class="record">
            <div class="record-data">
              {{parseInt(records.this_week_duration / 60)}}
              <span
                class="record-unit"
              >{{$i18n('min')}}</span>
            </div>
            <span class="record-data-name">{{$i18n('weeklyFocusDurations')}}</span>
          </div>
        </div>
        <div class="records-item">
          <div class="record">
            <div class="record-data">{{records.total_times}}</div>
            <span class="record-data-name">{{$i18n('totalFocusTimes')}}</span>
          </div>
          <div class="record">
            <div class="record-data">
              {{parseInt(records.total_duration / 60)}}
              <span class="record-unit">{{$i18n('min')}}</span>
            </div>
            <span class="record-data-name">{{$i18n('totalFocusDurations')}}</span>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
/* eslint-disable no-debugger */
/* eslint-disable no-console */
/* global chrome */

function formatTime(date, fmt) {
  fmt = fmt || "yyyy-MM-dd hh:mm:ss";
  var date = new Date(date);
  var o = {
    "M+": date.getMonth() + 1, //
    "d+": date.getDate(), //
    "h+": date.getHours(), //
    "m+": date.getMinutes(), //
    "s+": date.getSeconds(), //
    "q+": Math.floor((date.getMonth() + 3) / 3), //
    S: date.getMilliseconds() //
  };
  if (/(y+)/.test(fmt))
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + "").substr(4 - RegExp.$1.length)
    );
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt))
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length)
      );
  return fmt;
}

export default {
  name: "Records",
  components: {},
  props: {
    userInfos: {
      type: Object
    }
  },
  watch: {
    async userInfos() {
      setTimeout(() => {
        this.initRecords();
      }, 300);
    }
  },
  data: () => {
    return {
      records: null
    };
  },

  methods: {
    async initRecords() {
      let bgPage = chrome.extension.getBackgroundPage();
      this.records = await bgPage.records.fetchRecords();
    }
  },

  created() {
    this.initRecords();
  }
};
</script>

