<template>
  <div>
    <template>
      <div class="histories-container">
        <div class="setting-main-switch">
          <div class="setting-infos">
            <h2 class="setting-name">{{$i18n('pageHistoriesTitle')}}</h2>
            <p class="setting-desc">{{$i18n('pageHistoriesDesc')}}</p>
          </div>
        </div>
        <div class="histories">
          <template v-if="histories.length">
            <div class="oneday" v-if="histories[0].date !== today">
              <h3 class="block-name">{{$i18n("pageHistoriesToday")}}</h3>
              <h3 class="block-desc">{{$i18n("pageHistoriesEmpty")}}</h3>
            </div>

            <div class="oneday" v-for="history in histories" :key="history.date">
              <h3 class="block-name">{{formatDate(history.date)}}</h3>
              <h3
                class="block-desc"
              >{{$i18n('pageHistoriesDuration',formatDuration(history.totalDuration))}}</h3>
              <div class="records">
                <div
                  class="record"
                  v-for="record in history.records"
                  :key="`${history.date}-${record.domain}`"
                >
                  <img class="record-icon" :src="getFavicon(record.domain)" @error="fixIcon">
                  <div class="record-infos-container">
                    <div class="record-infos">
                      <a
                        class="record-infos-domain"
                        target="_blank"
                        :href="`http://${record.domain}`"
                      >{{record.domain}}</a>
                      <div class="percentage-bar-denominator">
                        <div
                          class="percentage-bar-numerator"
                          :style="{width: `${Math.ceil((record.duration || 1) / maximum * 100)}%`}"
                        ></div>
                      </div>
                    </div>
                    <div class="record-duration">{{formatDuration(record.duration || 1)}}</div>
                  </div>
                </div>
              </div>
            </div>
            <div class="more" v-if="loading">{{$i18n('pageHistoriesLoading')}}</div>
            <div class="more" v-else-if="finish">{{$i18n('pageHistoriesNoMore')}}</div>
            <div class="more load-more" @click="loadMore" v-else>
              {{$i18n('pageHistoriesMore')}}
              <img :src="require('@/assets/more.png')">
            </div>
          </template>
          <template v-else>
            <div class="oneday">
              <h3 class="block-name">{{$i18n("pageHistoriesToday")}}</h3>
              <h3 class="block-desc">{{$i18n("pageHistoriesEmpty")}}</h3>
            </div>
          </template>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
/* eslint-disable no-debugger */
/* eslint-disable no-console */
/* global chrome */

import SiteIcon from "@/assets/site-icon.png";

function getTodayRange() {
  var now = new Date(); //
  var nowDay = now.getDate(); //
  var nowMonth = now.getMonth(); //
  var nowYear = now.getFullYear(); //
  var todayStartedAt = new Date(nowYear, nowMonth, nowDay);
  var todayEndedAt = new Date(nowYear, nowMonth, nowDay, 23, 59, 59);
  return [+todayStartedAt, +todayEndedAt];
}

function formatTime(date, fmt) {
  fmt = fmt || "yyyy-MM-dd hh:mm:ss";
  date = new Date(date);
  var o = {
    "M+": date.getMonth() + 1, //
    "d+": date.getDate(), //
    "h+": date.getHours(), //
    "m+": date.getMinutes(), //
    "s+": date.getSeconds(), //
    "q+": Math.floor((date.getMonth() + 3) / 3), //
    S: date.getMilliseconds() //
  };
  if (/(y+)/.test(fmt))
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + "").substr(4 - RegExp.$1.length)
    );
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt))
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length)
      );
  return fmt;
}

function genReportDatas(recordDict) {
  let totalDuration = 0;
  let maximumDuration = 0;
  const recordKeys = Object.keys(recordDict);
  const reportDatas = recordKeys.map(key => {
    const duration = recordDict[key];
    totalDuration += duration;
    if (duration > maximumDuration) {
      maximumDuration = duration;
    }
    return {
      domain: key,
      duration
    };
  });
  reportDatas.sort((a, b) => {
    return b.duration - a.duration;
  });
  return { totalDuration, maximumDuration, reportDatas };
}

export default {
  name: "PageHistories",
  components: {},
  props: {
    userInfos: {
      type: Object
    }
  },
  watch: {
    async userInfos() {
      this.reset();
      this.initHistories();
    }
  },
  data: () => {
    const today = new Date();
    return {
      nextDate: parseInt(formatTime(today, "yyyyMMdd")),
      today: parseInt(formatTime(today, "yyyyMMdd")),
      yesterday: parseInt(
        formatTime(today.setDate(today.getDate() - 1), "yyyyMMdd")
      ),
      maximum: 0,
      histories: [],
      finish: false,
      loading: true
    };
  },

  methods: {
    getFavicon(url) {
      let fullUrl;
      if (url.startsWith("http://")) {
        fullUrl = url;
      } else if (url.startsWith("https://")) {
        fullUrl = url;
      } else {
        fullUrl = `http://${url}`;
      }
      return `${fullUrl}/favicon.ico`;
    },
    fixIcon(e) {
      const img = e.target;
      img.setAttribute("src", null);
      img.setAttribute("src", SiteIcon);
    },
    parseDate(dateNum) {
      const [, year, month, day] = /(\d{4})(\d{2})(\d{2})/.exec(dateNum);
      return `${year}-${month}-${day}`;
    },
    formatDuration(duration) {
      const totalMin = parseInt(duration / 60);
      const sec = duration % 60;
      const min = totalMin % 60;
      const hour = parseInt(totalMin / 60);
      if (hour) {
        return `${hour} ${this.$i18n("simplifiedHr")} ${min} ${this.$i18n(
          "simplifiedMin"
        )}`;
      } else if (min) {
        return `${min} ${this.$i18n("simplifiedMin")}`;
      } else {
        return `${sec} ${this.$i18n("simplifiedSec")}`;
      }
    },
    formatDate(date) {
      if (date === this.today) {
        return this.$i18n("pageHistoriesToday");
      } else if (date === this.yesterday) {
        return this.$i18n("pageHistoriesYesterday");
      } else {
        return this.parseDate(date);
      }
    },

    loadMore() {
      if (this.userInfos) {
        this.fetchHistories();
      } else {
        this.$bus.$emit("showLoginModal");
      }
    },

    async fetchHistories() {
      this.loading = true;
      try {
        const dayReport = await this.$request({
          url: "/v1/chrome_ext/page_histories/day_report",
          params: {
            date: this.nextDate
          }
        });
        const date = dayReport.date;
        const recordDict = dayReport.records;
        const { totalDuration, maximumDuration, reportDatas } = genReportDatas(
          recordDict
        );
        if (maximumDuration > this.maximum) {
          this.maximum = maximumDuration;
        }

        if (this.nextDate === this.today) {
          this.histories = [];
        }

        this.histories.push({
          date,
          totalDuration,
          records: reportDatas
        });

        let nextDate = new Date(this.parseDate(date));
        nextDate = nextDate.setDate(nextDate.getDate() - 1);
        this.nextDate = parseInt(formatTime(nextDate, "yyyyMMdd"));
      } catch (e) {
        if (e.msg === "cannot find this date's report") {
          this.finish = true;
        }
      }
      this.loading = false;
    },

    async getLocaoHistories() {
      let bgPage = chrome.extension.getBackgroundPage();
      const localReports = await bgPage.pageHistories.getLocalLogs();

      const recordDict = {};
      const todayRange = getTodayRange();

      localReports.forEach(log => {
        let endedAt = log.started_at + log.duration;
        let duration = 0;
        if (log.started_at >= todayRange[0]) {
          endedAt = endedAt <= todayRange[1] ? endedAt : todayRange[1];
          duration = endedAt - log.started_at;
        } else if (endedAt >= todayRange[0] && endedAt <= todayRange[1]) {
          duration = endedAt - todayRange[0];
        } else {
          return;
        }
        duration = parseInt(duration / 1000);
        const domain = log.url.split("/")[2];
        if (recordDict[domain]) {
          recordDict[domain] += duration;
        } else {
          recordDict[domain] = duration;
        }
      });

      const { totalDuration, maximumDuration, reportDatas } = genReportDatas(
        recordDict
      );
      if (maximumDuration > this.maximum) {
        this.maximum = maximumDuration;
      }

      this.loading = false;
      this.histories = [
        {
          date: this.today,
          totalDuration,
          records: reportDatas
        }
      ];
    },

    initHistories() {
      const today = new Date();
      this.today = parseInt(formatTime(today, "yyyyMMdd"));
      this.yesterday = parseInt(
        formatTime(today.setDate(today.getDate() - 1), "yyyyMMdd")
      );
      if (this.userInfos) {
        this.fetchHistories();
      } else {
        this.getLocaoHistories();
      }
    },
    reset() {
      const today = new Date();
      this.nextDate = parseInt(formatTime(today, "yyyyMMdd"));
      this.today = parseInt(formatTime(today, "yyyyMMdd"));
      this.yesterday = parseInt(
        formatTime(today.setDate(today.getDate() - 1), "yyyyMMdd")
      );
      this.maximum = 0;
      this.histories = [];
      this.finish = false;
      this.loading = true;
    }
  },

  created() {
    this.initHistories();
  }
};
</script>

