<template>
  <div>
    <div class="work-mode-container" :class="{off: !workMode}">
      <div class="setting-main-switch">
        <div class="setting-infos">
          <h2 class="setting-name">
            {{$i18n('settingsWorkModeTitle')}}
            <md-button class="md-icon-button md-dense" @click="workModeTipsDisplay = true;">
              <md-icon>help_outline</md-icon>
            </md-button>
          </h2>
          <p class="setting-desc">{{$i18n('settingsWorkModeDesc')}}</p>
        </div>
        <md-switch v-model="workMode"></md-switch>
      </div>
      <h3 class="block-name">{{$i18n('sectionWorkModeTitle')}}</h3>
      <div class="setting-option">
        <label>{{$i18n('sectionWorkModeSwitch_focusDuration')}}</label>
        <div class="select-container">
          <md-field>
            <template v-if="focusDurationOptions.defaultOptions">
              <md-select v-model="focusDuration">
                <md-optgroup :label="$i18n('customDuration')">
                  <md-option
                    :value="focusDurationOptions.currentOption.value"
                  >{{focusDurationOptions.currentOption.text}} {{$i18n('unitMin')}}</md-option>
                </md-optgroup>
                <md-optgroup :label="$i18n('defaultDuration')">
                  <md-option
                    v-for="option in focusDurationOptions.defaultOptions"
                    :key="option.text"
                    :value="option.value"
                  >{{option.text}} {{$i18n('unitMin')}}</md-option>
                </md-optgroup>
              </md-select>
            </template>
            <template v-else>
              <md-select v-model="focusDuration">
                <md-option
                  v-for="option in focusDurationOptions"
                  :key="option.text"
                  :value="option.value"
                >{{option.text}} {{$i18n('unitMin')}}</md-option>
              </md-select>
            </template>
          </md-field>
        </div>
      </div>
      <div class="setting-option">
        <label>{{$i18n('sectionWorkModeSwitch_breakDuration')}}</label>
        <div class="select-container">
          <md-field>
            <md-select v-model="breakDuration">
              <md-option
                v-for="option in breakDurationOptions"
                :key="option.text"
                :value="option.value"
              >{{option.text}} {{$i18n('unitMin')}}</md-option>
            </md-select>
          </md-field>
        </div>
      </div>
      <div class="setting-option">
        <label>{{$i18n('sectionWorkModeSwitch_longBreakDuration')}}</label>
        <div class="select-container">
          <md-field>
            <md-select v-model="longBreakDuration">
              <md-option
                v-for="option in longBreakDurationOptions"
                :key="option.text"
                :value="option.value"
              >{{option.text}} {{$i18n('unitMin')}}</md-option>
            </md-select>
          </md-field>
        </div>
      </div>
      <div class="setting-option">
        <label>{{$i18n('sectionWorkModeSwitch_longBreakFrequency')}}</label>
        <div class="select-container">
          <md-field>
            <md-select v-model="longBreakFrequency">
              <md-option
                v-for="option in longBreakFrequencyOptions"
                :key="option.text"
                :value="option.value"
              >{{option.text ? `${option.text} ${$i18n('unitTimes')}` : $i18n('never')}}</md-option>
            </md-select>
          </md-field>
        </div>
      </div>
      <h3 class="block-name">{{$i18n('sectionAtomaticTitle')}}</h3>
      <div class="setting-option">
        <label>{{$i18n('sectionAtomatic_autoBreak')}}</label>
        <md-switch v-model="autoBreak"></md-switch>
      </div>
      <div class="setting-option">
        <label>{{$i18n('sectionAtomatic_autoFocus')}}</label>
        <md-switch v-model="autoFocus"></md-switch>
      </div>
      <div class="sub-setting-option" :style="{height: `${autoFocus ? 64 : 0}px`}">
        <div class="setting-option">
          <label>{{$i18n('sectionAtomatic_autoFocusCycle')}}</label>
          <div class="select-container">
            <md-field>
              <md-select v-model="autoFocusCycle">
                <md-option
                  v-for="option in autoFocusCycleOptions"
                  :key="option.text"
                  :value="option.value"
                >{{option.text ? `${option.text} ${$i18n('unitTimes')}` : $i18n('unlimited')}}</md-option>
              </md-select>
            </md-field>
          </div>
        </div>
      </div>
    </div>

    <md-dialog-alert
      :md-active.sync="workModeTipsDisplay"
      :md-title="$i18n('tipsWorkModeTitle')"
      :md-content="$i18n('tipsWorkModeContent')"
      :md-confirm-text="$i18n('tipsWorkModeButton')"
    />
  </div>
</template>

<script>
import { Promise } from "q";
/* eslint-disable no-debugger */
/* eslint-disable no-console */
/* global chrome */

const focusDurations = [
  5,
  8,
  10,
  15,
  20,
  25,
  30,
  35,
  40,
  45,
  50,
  55,
  60,
  70,
  80,
  90,
  100,
  110,
  120
];
const breakDurations = [1, 2, 3, 5, 8, 10, 15, 20, 25, 30, 35, 40, 45];
const longBreakDurations = [5, 8, 10, 15, 20, 25, 30, 35, 40, 45, 50, 55, 60];
const longBreakFrequency = [0, 2, 3, 4, 5, 6];
const autoFocusCycles = [0, 2, 3, 4, 5, 6, 7, 8, 9, 10];

export default {
  name: "SettingsWorkMode",
  components: {},
  data: () => {
    const breakDurationOptions = breakDurations.map(min => {
      return { text: min, value: min * 60000 };
    });
    const longBreakDurationOptions = longBreakDurations.map(min => {
      return { text: min, value: min * 60000 };
    });
    const longBreakFrequencyOptions = longBreakFrequency.map(times => {
      return { text: times, value: times };
    });
    const autoFocusCycleOptions = autoFocusCycles.map(times => {
      return { text: times, value: times };
    });

    const bgPage = chrome.extension.getBackgroundPage();

    return {
      breakDurationOptions,
      longBreakDurationOptions,
      longBreakFrequencyOptions,
      autoFocusCycleOptions,

      workModeTipsDisplay: false,
      bgPage,
      workMode: bgPage.settings.workMode,
      focusDuration: bgPage.settings.focusDuration,
      breakDuration: bgPage.settings.breakDuration,
      longBreakDuration: bgPage.settings.longBreakDuration,
      longBreakFrequency: bgPage.settings.longBreakFrequency,
      autoFocus: bgPage.settings.autoFocus,
      autoBreak: bgPage.settings.autoBreak,
      autoFocusCycle: bgPage.settings.autoFocusCycle
    };
  },
  computed: {
    focusDurationOptions() {
      const focusDuration = parseInt(this.focusDuration / 60000);
      if (focusDurations.includes(focusDuration)) {
        return focusDurations.map(min => {
          return {
            text: min,
            value: min * 60000
          };
        });
      } else {
        return {
          defaultOptions: focusDurations.map(min => {
            return {
              text: min,
              value: min * 60000
            };
          }),
          currentOption: {
            text: focusDuration,
            value: focusDuration * 60000
          }
        };
      }
    }
  },
  watch: {
    workMode(val) {
      if (this.bgPage.settings.workMode !== val) {
        this.bgPage.settings.workMode = val;
      }
    },
    focusDuration(val) {
      if (this.bgPage.settings.focusDuration !== val) {
        this.bgPage.settings.focusDuration = val;
      }
    },
    breakDuration(val) {
      if (this.bgPage.settings.breakDuration !== val) {
        this.bgPage.settings.breakDuration = val;
      }
    },
    longBreakDuration(val) {
      if (this.bgPage.settings.longBreakDuration !== val) {
        this.bgPage.settings.longBreakDuration = val;
      }
    },
    longBreakFrequency(val) {
      if (this.bgPage.settings.longBreakFrequency !== val) {
        this.bgPage.settings.longBreakFrequency = val;
      }
    },
    autoFocus(val) {
      if (this.bgPage.settings.autoFocus !== val) {
        this.bgPage.settings.autoFocus = val;
      }
    },
    autoBreak(val) {
      if (this.bgPage.settings.autoBreak !== val) {
        this.bgPage.settings.autoBreak = val;
      }
    },
    autoFocusCycle(val) {
      if (this.bgPage.settings.autoFocusCycle !== val) {
        this.bgPage.settings.autoFocusCycle = val;
      }
    }
  },
  methods: {
    async settingItem(key, defaultValue) {
      return new Promise(resolve => {
        chrome.storage.sync.get(key, res => {
          const val = res[key];
          if (val === undefined) {
            resolve(defaultValue);
          } else {
            resolve(val);
          }
        });
      });
    }
  },
  mounted() {
    chrome.storage.onChanged.addListener(changes => {
      if (changes.settings) {
        const newSettings = changes.settings.newValue;
        this.workMode = newSettings.workMode;
        this.focusDuration = newSettings.focusDuration;
        this.breakDuration = newSettings.breakDuration;
        this.longBreakDuration = newSettings.longBreakDuration;
        this.longBreakFrequency = newSettings.longBreakFrequency;
        this.autoFocus = newSettings.autoFocus;
        this.autoBreak = newSettings.autoBreak;
        this.autoFocusCycle = newSettings.autoFocusCycle;
      }
    });
  },
  beforeDestroy() {}
};
</script>

<style lang="less" scoped>
.work-mode-container {
  border-radius: 8px;
  border: 1px solid @G5;
  transition: max-height 0.3s ease-out;
  max-height: 760px;
  padding-bottom: 24px;
  overflow: hidden;
}

.work-mode-container.off {
  transition: max-height 0.3s ease-in;
  max-height: 136px;
}
.sub-setting-option {
  overflow: hidden;
  transition: height 0.3s ease-in;
}
</style>