<template>
  <div id="app" v-title :class="{floatTop:floatTopbar}">
    <div class="topbar">
      <div class="topbar-container">
        <img id="logo" :src="$i18n('logoPath')" />
        <md-button
          class="logout-btn"
          v-if="userInfos"
          @click="showLogoutDialog"
        >{{$i18n('logoutBtn')}}</md-button>
      </div>
    </div>
    <div class="navs">
      <div class="navs-width-limit">
        <router-link class="route" :to="{ name: 'Infos'}">
          <md-icon>equalizer</md-icon>
          <span>{{$i18n('navStatistics')}}</span>
        </router-link>
        <router-link class="route" :to="{ name: 'Settings'}">
          <md-icon>settings</md-icon>
          <span>{{$i18n('navSettings')}}</span>
        </router-link>
      </div>
    </div>
    <div class="func-container">
      <div class="width-limit">
        <router-view :user-infos="userInfos"></router-view>
      </div>
    </div>
    <div class="footer">
      <a class="link" target="_blank" :href="$i18n('privacyPolicyLink')">{{$i18n('privacyPolicy')}}</a>
      <span class="split"></span>
      <mailto class="link" :user-infos="userInfos">{{$i18n('feedback')}}</mailto>
    </div>
    <md-snackbar class="error-snackbar" :md-active.sync="showError" md-persistent>
      <span>{{errorMsg}}</span>
    </md-snackbar>
    <md-dialog-confirm
      :md-active.sync="showLogout"
      :md-title="$i18n('logoutDialogTitle')"
      :md-content="$i18n('logoutDialogContent')"
      :md-confirm-text="$i18n('logoutDialogConfirmBtn')"
      :md-cancel-text="$i18n('logoutDialogCancelBtn')"
      @md-confirm="logout"
    />
  </div>
</template>

<script>
/* eslint-disable no-debugger */
/* eslint-disable no-console */
/* global chrome */
import Mailto from "@/components/mailto";

export default {
  name: "Options",
  components: { Mailto },
  data: () => {
    return {
      floatTopbar: false,
      userInfos: null,
      bgPage: null,
      showLogout: false,
      showError: false,
      errorMsg: "",
      activedNav: ""
    };
  },
  methods: {
    showLogoutDialog() {
      this.showLogout = true;
    },
    async logout() {
      this.bgPage.logout();
      this.userInfos = null;
    },
    goAnchor(selector, auto) {
      this.activedNav = selector;
      this.$el.querySelector(selector).scrollIntoView({
        behavior: auto ? "auto" : "smooth"
      });
    },
    debounce(func, wait, immediate) {
      var timeout, args, context, timestamp, result;
      var later = function() {
        // 
        var last = new Date() - timestamp;
        // lastwait
        if (last < wait && last > 0) {
          timeout = setTimeout(later, wait - last);
        } else {
          timeout = null;
          // immediate===true
          if (!immediate) {
            result = func.apply(context, args);
            if (!timeout) context = args = null;
          }
        }
      };

      return function() {
        context = this;
        args = arguments;
        timestamp = new Date();
        var callNow = immediate && !timeout;
        // 
        if (!timeout) timeout = setTimeout(later, wait);
        if (callNow) {
          result = func.apply(context, args);
          context = args = null;
        }

        return result;
      };
    },
    initActivedNav() {
      const pageYOffset = window.pageYOffset;
      const anchors = document.querySelectorAll(".anchor");
      let activedNav = "";
      anchors.forEach(anchor => {
        if (pageYOffset >= anchor.offsetTop) {
          activedNav = `#${anchor.id}`;
        }
      });
      this.activedNav = activedNav;
    },
    handleScroll() {
      if (window.scrollY <= 0) {
        if (this.floatTopbar) {
          this.floatTopbar = false;
        }
      } else if (!this.floatTopbar) {
        this.floatTopbar = true;
      }
    }
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
    let bgPage = chrome.extension.getBackgroundPage();
    this.bgPage = bgPage;
    this.userInfos = bgPage.userInfos;
    this.bgPage.initUserInfos();

    chrome.runtime.onMessage.addListener(msg => {
      switch (msg.type) {
        case "logined":
          this.userInfos = msg.userInfos;
          break;
        case "logout":
          this.userInfos = null;
          break;
        default:
          break;
      }
    });

    this.$bus.$on("error", msg => {
      this.errorMsg = msg;
      this.showError = true;
    });
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll);
    this.$bus.$off("error");
  }
};
</script>

