/* global chrome */

import "@/utils/base64.min.js";
import "@/assets/roboto.css";
import "@/assets/media-query.less";

import Vue from 'vue'
import VueRouter from 'vue-router'
import AsyncComputed from 'vue-async-computed'
import Request from '@/plugins/request'
import I18n from '@/plugins/i18n'
import Bus from '@/plugins/bus'

import VueMaterial from 'vue-material'
import 'vue-material/dist/vue-material.min.css'

import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';

import 'chrome-extension-async'

Vue.config.productionTip = false

Vue.use(VueMaterial)
Vue.use(VueRouter)
Vue.use(I18n)
Vue.use(AsyncComputed)
Vue.use(Request)
Vue.use(Bus)

Vue.use(Loading, {
  color: "#FF6757"
});

// Vue.use(VueProgressBar, {
//   color: '#ff6757',
//   failedColor: 'red',
//   height: '2px'
// })

Vue.directive('title', {
  inserted: function () {
    document.title = chrome.i18n.getMessage('optionsPageTitle');
  }
});

import Root from './Options.vue'
import Settings from './pages/Settings.vue'
import Infos from './pages/Infos.vue'

const routes = [{
    path: '/',
    redirect: {
      name: 'Infos'
    }
  },
  {
    name: 'Settings',
    path: '/settings',
    component: Settings,
    meta: {
      title: chrome.i18n.getMessage('optionsPageTitleSettings')
    }
  },
  {
    name: 'Infos',
    path: '/infos',
    component: Infos,
    meta: {
      title: chrome.i18n.getMessage('optionsPageTitleInfos')
    }
  }
]
const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
    setTimeout(() => {
      document.title = to.meta.title
    }, 0);
  }
  next()
})

new Vue({
  router,
  render(h) {
    return h(Root)
  }
}).$mount('#app')

window._gaq = window._gaq || [];
window._gaq.push(['_setAccount', 'UA-75103409-6']);
window._gaq.push(['_trackPageview']);

(function () {
  var ga = document.createElement('script');
  ga.type = 'text/javascript';
  ga.async = true;
  ga.src = 'https://ssl.google-analytics.com/ga.js';
  var s = document.getElementsByTagName('script')[0];
  s.parentNode.insertBefore(ga, s);
})();