<template>
  <div>
    <div class="user-wrapper">
      <template v-if="!userInfos">
        <md-avatar class="md-avatar-icon unlogined">
          <md-icon>person</md-icon>
        </md-avatar>
        <div class="user-infos">
          <h3>{{$i18n('greeting')}}</h3>
          <p>{{$i18n('greetingDesc')}}</p>
        </div>
        <md-button class="md-primary md-raised" @click="requestLogin">{{$i18n('loginBtn')}}</md-button>
      </template>
      <template v-else>
        <md-avatar v-if="userInfos.avatar">
          <img :src="userInfos.avatar" alt="Avatar">
        </md-avatar>
        <md-avatar class="md-avatar-icon" v-else>
          <md-icon>person</md-icon>
        </md-avatar>

        <div class="user-infos">
          <h3>{{userInfos.nickname}}</h3>
          <p>{{$i18n('loginedDesc', `${usedDuration}`)}}</p>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
/* eslint-disable no-debugger */
/* eslint-disable no-console */
/* global chrome */

export default {
  name: "UserInfos",
  props: {
    userInfos: {
      type: Object
    }
  },
  computed: {
    usedDuration() {
      if (!this.userInfos) {
        return 0;
      }
      const createdAt = new Date(this.userInfos.created_at * 1000);
      createdAt.setSeconds(0);
      createdAt.setMinutes(0);
      createdAt.setHours(0);
      const usedDuration = parseInt((new Date() - createdAt) / 86400000) + 1;
      return usedDuration;
    }
  },

  methods: {
    requestLogin() {
      this.bgPage.requestLogin();
    }
  },
  created() {
    let bgPage = chrome.extension.getBackgroundPage();
    this.bgPage = bgPage;
    this.$bus.$on("showLoginModal", () => {
      this.requestLogin();
    });
  },
  beforeDestroy() {
    this.$bus.$off("showLoginModal");
  }
};
</script>

